import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';
import { IonicPage, NavController, NavParams,AlertController,MenuController } from 'ionic-angular';

import { ApiProvider } from '../../providers/api/api';
import { AuthProvider } from '../../providers/auth/auth';
import { ServiceProvider } from '../../providers/service/service';

@IonicPage()
@Component({
  selector: 'page-user',
  templateUrl: 'signup.html'
})
export class SignupPage {
  data: any = {};
 // signup: UserOptions = { username: '', password: '' };
  submitted = false;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public api:ApiProvider,
    private AuthService: AuthProvider,
    public alertCtrl: AlertController,
    private service: ServiceProvider
  ) {}

  onSignup(form: NgForm) {
    this.submitted = true;

    if (form.valid) {
      // this.userData.signup(this.signup.username);
      // this.navCtrl.push(TabsPage);
    }
  }

  signup(data){

    if(data.password == data.conf_password) {
      this.api.post('signupuser',data).subscribe((response : any)  => {
        console.log(response);
        if(response.Ack === 1){
            this.afloginsuccess(response);
        }else{
          this.service.popup('Alert', response.msg);
        }
      }, err => {
        this.service.popup('Alert', 'Already Registered');
      });
    }
    else {
      this.service.popup('Failed', 'Both password should be matched!!');
    }
   

  }

  afloginsuccess(response){
  
    this.navCtrl.setRoot('LoginPage');
    this.service.popup('Success', 'User successfully created');
  }

  onSignin() {
    this.navCtrl.push("LoginPage");
   }
}



